.change-password-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 50px;
  .change-password-form {
    max-width: 500px;
    width: 100%;
    .form-control-custom {
      padding-bottom: 10px;
    }
    .number-container {
      display: flex;
      align-items: center;
      input {
        text-align: center;
      }
      .plus-symbol {
        padding: 12px;
        color: #666;
      }
    }
    .button-container {
      display: flex;
      align-items: center;
      justify-content: space-around;
      padding-top: 10px;
      button {
        width: 200px;
      }
    }
  }
}
